.main {
    font-size: var(--base-font-size-large);
    padding: var(--page-padding);

    h5 {
        margin: calc(var(--base-gap-unit) * 2) calc(var(--base-gap-unit) * 8);
    }

    ul {
        margin: calc(var(--base-gap-unit) * 2) 0;
        padding-right: calc(var(--base-gap-unit) * 8);
    }

    @media screen and (max-width: 414px) {
        h5 {
            margin: calc(var(--base-gap-unit) * 2) calc(var(--base-gap-unit) * 2);
        }

        ul {
            padding: 0 0 0 calc(var(--base-gap-unit) * 12);
        }
    }
}

.profile {
    display: flex;
    @media screen and (max-width: 414px) {
        display: block;
    }
}

.capability {
    min-width: 350px;
    @media screen and (max-width: 1150px) {
        min-width: 340px;
    }
    @media screen and (max-width: 414px) {
        min-width: 100px;
    }
}

.description {
    margin: var(--block-margin);
}

.descriptionItem {
    margin-top: var(--block-padding);
}

.skills {
    display: flex;
    flex-wrap: wrap;

    & > div {
        width: 23%;
        margin: var(--base-gap-unit) 1%;
    }

    @media screen and (max-width: 414px) {
        & > div {
            width: 48%;
            height: 40px;
        }
    }
}
